.adcontainer {
    display: flex;
    flex-direction: column;
    height: 89.5vh;
    align-items: center;
    padding: 20px;
    background: url("C:\Users\ungati_mohith\Downloads\react-website\react-website-v1-master\public\images\ima-14.png") center center/cover no-repeat;
  }


  .form-container-policy {
    border: 4px ridge #000000;
    background-color: rgba(85, 85, 85, 0.507);
    padding: 20px;
    width: 80%;
    margin-top: 7px;
    border-radius: 15px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(1.5px);

  }

  .form-container-policy h1{
    color: azure;
    text-shadow: 
  -4px -1px 0 #00000063,  
   1px -1px 0 #00000063,
  -1px  1px 0 #00000063,
   1px  1px 0 #00000063;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }

  /* Updated styles */
  .form-container-policy .input-box input {

    width: 90%;
    height: 100%;
    background:transparent;
    border: 4px double #000000;
    border-radius: 30px;
    font-size: 16px;
    color: #000000;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding: 12px 20px;
    margin: 0 auto;
    outline: none;
    margin: 20px;
    margin-left: 45px;
  }
  
  .form-container-policy .input-box input::placeholder {
    color: #000000;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  /* Button styles */
  .form-container-policy button {
    width: 50%;
    height: 45px;
    background: #474747ca;
    outline: none;
    border-radius: 20px;
    border: none; /* Adjusted border for consistency */
    box-shadow: 2px 2px 3px rgb(16, 16, 16);
    color: #ffffffb9;
    font-size: 16px;
    cursor: pointer;
    font-weight: 700;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    transition: transform 0.2s ease;
    display: block;
    margin: 0 auto;
    margin-top: 20px;
  }
  
  .form-container-policy button:hover {
    background: #000000b9;
    color: #ffffffb9;
  }
  
  