.cust-sidebar {
    background: url('C:\Users\ungati_mohith\Downloads\react-website\react-website-v1-master\public\images\img-37.jpeg') bottom center/cover no-repeat;
    width: 108vh;
    height: 53vh;
    margin-left: 0.5vh;
    border-radius: 20px;
    border: 5px ridge #d9d9d9;
    padding: 20px;
    position: relative;
    top:1vh;
    left:7vh;
    z-index: 1;
    transition: transform 0.3s, opacity 0.3s;
    animation: pop 1s ease-in-out;
    opacity: 0;
    animation-fill-mode: forwards;
    box-shadow: 2px 2px 15px #2d2d2da8,-1px -1px 5px #ffffffad ;
  }
  
