.policy-list {
    height: 89.5vh;
    overflow-y: auto; 

  }
  
  .policy-list table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .policy-list th, .policy-list td {
    padding: 15px;
    text-align: left;
    border-bottom: 1px solid #ddd;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }
  
  .policy-list th {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    background-color: #4CAF50;
    color: white;
    position: sticky;
    top: 0; 
    z-index: 10; /* Ensures the header stays above the body */
  }
  
  .policy-list tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .policy-list tr:hover {
    background-color: #ddd;
  }
  
  .policy-list button {
    width: 60%;
    height: 40px;
    background: #43434368;
    border-radius: 30px;
    box-shadow: 2px 2px 7px rgba(43, 43, 43, 0.79);
    color: #2a2a2a;
    font-size: 20px;
    cursor: pointer;
    font-weight: 700;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    transition: transform 0.2s ease;
    margin-left: 20%;
  }
  
  .policy-list button:hover {
    background-color: #4CAF50;;
    color: #e2e2e2;
  }
  
  .policy-list span {
    cursor: pointer;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }


  .policy-list p{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 18px;
  }