.sbg {
    background: url('C:\Users\ungati_mohith\Downloads\react-website\react-website-v1-master\public\images\imag-4.jpg') center center/cover no-repeat;
    height: 89.5vh;
    width: 100%;
    position: fixed;
    padding: 0 50px;
  }
  
  .text-wrap {
    background: linear-gradient(90deg, rgba(28, 27, 27, 0.95) 0%, rgba(26, 23, 23, 0.85) 100%);
    color: white;
    width: 50%;
    height: 89.5vh;
    position: absolute;
    right: 0;
    top: 0;
    margin-right: 50px;
    padding: 20px;
  }

  
  .text-wrap h1 {
    text-align: left;
    font-size: 3rem;
    margin-bottom: 20px;
    margin-top: 20px;
  }

  .form-wrap input {
      width: 80%;
      background: transparent;
      border: none;
      outline: none;
      border: 2px solid #ffffff;
      border-radius: 30px;
      font-size: 16px;
      color: #ffffff;
      font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
      padding: 12px 20px 12px 20px;
      display: block;
      margin: 0 auto;
      margin-bottom: 30px;
      margin-left: 0;
    }
  
  .form-wrap input ::placeholder{
    color: #9c9c9c;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    
  }
  
  .hospital-form button {
    width: 50%;
    height: 45px;
    background: #51515186;
    border: 10px;
    outline: none;
    border-radius: 20px;
    border-color: #000000;
    box-shadow: 0 0 10px rgb(16, 16, 16);
    color: #ffffff;
    font-size: 16px;
    cursor: pointer;
    font-weight: 700;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    transition: transform 0.2s ease;
    display: block;
    margin-left: 0;
  }
  
  .hospital-form button:hover {
    background: #565656;
  }