.outershell {
    height: 89.5vh; 
    background: url("C:/Users/ungati_mohith/Downloads/react-website/react-website-v1-master/public/images/img-27.jpg") center center/cover no-repeat;
    background-attachment: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; 
  }
  
  .hospital-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px;
    height: 89.5vh; 
    overflow-y: auto; 
    background: rgba(255, 255, 255, 0.30); /* Slightly transparent background for readability */
    border-radius: 20px;
  }

  .hospital-list h1{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 49px;
    color: #000000;
    font-weight: normal;
    text-shadow: 
    -0px -0px 1px #ffffff,  
     0px -0px 1px #fff,
    -0px  0px 1px #ffffff,
     3px  2px 1px #0000007d;
  }

  .hospital-list p{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 22px;
    color: #000000;
    font-weight: normal;
  }
  
  .hospital-card {
    background: rgba(255, 255, 255, 0.29); 
    border-radius: 20px;
    box-shadow: 8px 8px 15px rgba(0, 0, 0, 0.15), -8px -8px 15px rgba(0, 0, 0, 0.15); /* Neumorphic shadow */
    margin: 20px;
    padding: 10px;
    width: 340px;
    transition: all 0.3s;
    border: 2px ridge #4CAF50;
  }
  
  .hospital-card:hover {
    transform: scale(1.025);
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3), -0px -0px 15px rgba(0, 0, 0, 0.3);
    background: rgba(255, 255, 255, 0.37);
    backdrop-filter: blur(3px); 
  }
  
  .hospital-name {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 25px;
    color: #000000;
    margin-bottom: 15px;
    font-weight: lighter;
  }
  
  .hospital-address {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 18px;
    color: #000000cf;
    margin: 5px;
  }
  