.nominee-container{
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(7, 1fr);
  gap: 15px;
  width: 100%;
  height: 89.5vh;
  position: fixed;
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
}
.nm-box{
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* box-shadow: 0 0 10px rgba(122, 122, 122, 0.441); */
}

.wrap h1{
  color: #fff;
  text-align: left;
  text-shadow: 3px 3px 3px rgba(0, 0, 0);
  /* text-shadow: 
  -4px -1px 0 #000,  
   1px -1px 0 #000,
  -1px  1px 0 #000,
   1px  1px 0 #000; */
  font-size: 70px;
  margin-top: 70px;
margin-bottom: 20px;
  margin-left: 5px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif;
}

.wrap{
  grid-column: 1 / 7;
  grid-row: 1 / 8;
  z-index: 99;
}

.wrap h2,.gdknw h2{
  color: #e9e9e9;
  text-align: left;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  margin-left: 5px;
  /* text-shadow: 
  -4px -1px 0 #000,  
   1px -1px 0 #000,
  -1px  1px 0 #000,
   1px  1px 0 #000; */
  font-size: 28px;
  margin-bottom: 8px;
  font-weight: lighter;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif;
}

.gdknw h3{
  color: #e9e9e9;
  text-align: left;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  margin-left: 5px;
  font-size: 20px;
  margin-bottom: 5px;
  margin-left: 5px;
  font-weight: lighter;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif;
}



.nm-box1 {
  grid-column: 1 / 7;
  grid-row: 1 / 5;
  position: relative;
  overflow: hidden;
}

.nm-box1 video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  filter: blur(1.5px);
}

