.gdknw{
  height: 100%;
  width: 100%;
  backdrop-filter: blur(2px);
}

.nominee-form{
  width: 100%;
  height: 100%;
  backdrop-filter: blur(2px);
  padding: 50px 50px;
  
}

.form-group input,
.form-group select {
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid #000000;
    border-radius: 30px;
    font-size: 16px;
    color: #000000;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    padding: 12px 20px 12px 20px;
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 30px;
  }

.form-group input ::placeholder{
  color: #9c9c9c;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif;
  
}

.nominee-form button {
  width: 50%;
  height: 45px;
  background: #51515186;
  border: 10px;
  outline: none;
  border-radius: 20px;
  border-color: #000000;
  box-shadow: 0 0 10px rgb(16, 16, 16);
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
  font-weight: 700;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  transition: transform 0.2s ease;
  display: block;
  margin: 0 auto;
}

.nominee-form button:hover {
  background: #434343;
}
