.claim-form-container {
    /* display: flex;
    justify-content: center;
    align-items: center; */
    height: 89.5vh;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    z-index: -1;
    padding-left: 3%;
    width: 65%;
  }
  
  
  .claim-form {
    background:transparent;
    width: 70%;
    z-index: 1;
  }
  
  .claim-form-container h2 {
    color: azure;
    font-size: 36px;
    text-shadow: 
  -4px -1px 2px #ffffff22,  
   1px -1px 2px #ffffff22,
  -1px  1px 2px #ffffff22,
   1px  1px 2px #ffffff22;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    margin-top: 50px;
    margin-bottom: 30px;
  }

  .claim-form-container a {
    color: #e9e9e9;
    text-align: left;
    text-shadow: 2px 2px 3px rgba(81, 81, 81, 0.651);
    font-size: 20px;
    font-weight: lighter;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    z-index: 1;

  }

  .claim-form input {
    width: 100%;
    height: 45px;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid #9c9c9c;
    border-radius: 30px;
    font-size: 16px;
    color: #9c9c9c;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding: 0 20px;
    margin-bottom: 30px;
    z-index: 1;
  }

  .claim-form input ::placeholder {
    color: #9c9c9c;
}
  
  .claim-form button {
    width: 60%;
    height: 45px;
    background: #43434368;
    border: none;
    outline: none;
    border-radius: 30px;
    box-shadow: 0 0 10px rgb(16, 16, 16);
    color: #d4d4d4;
    font-size: 16px;
    cursor: pointer;
    font-weight: 700;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    transition: transform 0.2s ease;
    margin-top: 20px;
    z-index: 1;
  }
  
  .claim-form button:hover {
    background: #4d4d4d;
    
  }
  
  .claim-bg{
    background-color: #2b2b2b;
    width:35%;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1000;
    height: 100vh;
  }

  .claim-bg p{
    color: #e9e9e9;
    text-align: left;
    text-shadow: 2px 2px 3px rgb(0, 0, 0);
    font-size: 22px;
    margin-bottom: 5px;
    margin-top: 64%;
    margin-left: 15px;
    font-weight: lighter;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }

  .claim-bg-1{
    background-color: #4f4f4f98;
    width:38%;
    position: absolute;
    right:7%;
    top: 12.5%;
    z-index: 1001;
    height: 32vh;
    padding-top: 32px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.712);
  }

  .claim-bg-1 h1{
    color: azure;
    font-size: 48px;
    text-shadow: 
  -4px -1px 0 #00000063,  
   1px -1px 0 #00000063,
  -1px  1px 0 #00000063,
   1px  1px 0 #00000063;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }