
table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  th, td {
    padding: 12px 15px;
    text-align: left;
    border-bottom: 1px solid #ddd;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }
  
  th {
    font-size: 17px;
    font-weight: bold;
    background-color: #4CAF50;
    color: white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }
  
  tbody tr {
    transition: background-color 0.3s;
  }
  
  tbody tr:hover {
    background-color: #f1f1f1;
  }
  
.claim-details-2 button {
    width: 60%;
    height: 50px;
    background: #43434368;
    border-radius: 30px;
    box-shadow: 0 0 10px rgb(16, 16, 16);
    color: #d4d4d4;
    font-size: 20px;
    cursor: pointer;
    font-weight: 700;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    transition: transform 0.2s ease;
    margin-top: 25px;
    margin-left: 18%;
  }

  .claim-details-2 .simp{
    height: 89.5vh;
    border-top-left-radius: 200px;
    border-bottom-left-radius: 400px;
    backdrop-filter: blur(4px);
    padding-top: 35px;
    padding-left: 60px;
  }

  .claim-details-2 h1{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: azure;
    font-size: 45px;
    font-weight: bolder;
    text-shadow: 
  0px 1px 10px #000000,  
   1px -1px 9px #8d8d8de8,
  2px  1px 6px #c1c1c1e8,
   1px  1px 10px #000000;
  }

  .claim-details-2 p{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: rgb(255, 255, 255);
    font-size: 25px;
    text-shadow: 
  0px 1px 10px #000000,  
   1px -1px 9px #8d8d8de8,
  2px  1px 6px #c1c1c1e8,
   1px  1px 10px #000000;
  }
  
  .claim-details input[type="text"] {
    padding: 10px;
    font-size: 17px;
    border: 1.5px solid #000000;
    border-radius: 5px;
    width: calc(100% - 22px);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }

  .claim-details input::placeholder {
    font-size: 17px;
    color: #00000088;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }

  .hmm select{
    background-color: #4CAF50;
    font-size: 17px;
    font-weight: bold;
    color: white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    border: none;
  }

  .hmm option{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }
  
  .claim-details {
    position: absolute;
    background: #fff;
    height: 89.5vh;
    width: 50%;
    padding: 10px;
    z-index: -1;
  }

  .claim-details-2 {
    background: url("C:\Users\ungati_mohith\Downloads\react-website\react-website-v1-master\public\images\img-23.jpg") bottom center/cover no-repeat;
    position: absolute;
    right: 0;
    height: 91vh;
    width: 115vh;
    border-top-left-radius: 220px;
    border-bottom-left-radius: 460px;    
    z-index: 10;
    border: 3px solid #000000;
    box-shadow: 2px 10px 19px #000000;
  }

  .claim-details-wrapper {
    background-color: #ffffff;
    position: absolute;
    top: 52px;
    right: 0;
    height: 90.7vh;
    width: 135vh;
    border-top-left-radius: 280px;
    border-bottom-left-radius: 460px;    
    z-index: 9;
    position: fixed;
    border-bottom-left-radius: 900px;
    border: 3px solid #000000;
    box-shadow: 2px 10px 19px #000000;

  }
  
  .hmm a {
    text-decoration: none;
    color: #4CAF50;
  }
  
  .hmm a:hover {
    text-decoration: underline;
  }

  .claim-details i{
    zoom: 1.5;
  }

  .claim-details p {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    text-align: left;
    font-size: 23px;
    margin-left: 0;
  }

  .claim-details-2 button:nth-of-type(1):hover {
    background-color: #f8f8f8ac;
    color: #131313ce;
  }
  
  .claim-details-2 button:nth-of-type(2):hover {
    background-color: #000000cb; 
    color: #ececece2;
  }

