.class-list-wrapper {
  height: 100vh;
  width: 100vw;
  /* background-color: #f1f1f1; */
  background: url("C:/Users/ungati_mohith/Downloads/react-website/react-website-v1-master/public/images/img-40.jpg") center center/cover no-repeat;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden; 
}

.claim-view-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  height: 100%;
  overflow-y: auto; 
  background: transparent;
  position: relative; /* To ensure it scrolls within the fixed container */
  z-index: 1;
}

.claim-list {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.claim-card h2{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 25px;
  font-weight: bold;
margin-left: 30px;
}

.claim-card p{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 23px;
  font-weight: lighter;
margin: 10px;
}

.claim-card {
  width: 180vh;
  background-color: #f1f1f161 ;
  backdrop-filter: blur(2px);
  border-radius: 25px;
  margin-top: 20px;
  cursor: pointer;
  border: 4px ridge rgba(60, 89, 24, 0.792);
  transition: all 0.3s;
}

.claim-card:hover {
  box-shadow: 2px 4px 20px rgba(30, 30, 30, 0.765), -2px -4px 20px rgba(0, 0, 0, 0.765);
  background-color: #f1f1f1ac ;
  width: 185vh;
}

.admin-remarks {
  height: 50px;
  margin: 10px;
  background: #f1f1f199;
  border-radius: 12px;
  border: 2px ridge rgba(60, 89, 24, 0.792);
  box-shadow: 2px 2px 10px rgba(30, 30, 30, 0.753)
}
