.claim-form-container-2 {
    /* display: flex;
    justify-content: center;
    align-items: center; */
    position: fixed;
    height: 89.5vh;
    /* background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%); */
    z-index: -1;
    padding-left: 3%;
    width: 100%;
  }
  .claim-form-2 {
    background:transparent;
    width: 40%;
    position: absolute;
    right:3%;
    top: 10%;
    z-index: 1;
  }
  
  .claim-form-container-2 h2 {
    color: azure;
    font-size: 36px;
    text-shadow: 
  -4px -1px 2px #ffffff22,  
   1px -1px 2px #ffffff22,
  -1px  1px 2px #ffffff22,
   1px  1px 2px #ffffff22;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    margin-top: 50px;
    margin-bottom: 30px;
  }

  .claim-form-container-2 a {
    color: #e9e9e9;
    text-align: left;
    text-shadow: 2px 2px 3px rgba(81, 81, 81, 0.651);
    font-size: 20px;
    font-weight: lighter;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    z-index: 1;

  }

  .claim-form-2 input {
    width: 100%;
    height: 45px;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid #202020;
    border-radius: 30px;
    font-size: 16px;
    color: #202020;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding: 0 20px;
    margin-bottom: 30px;
    z-index: 1;
  }

  .claim-form-2 input ::placeholder {
    color: #202020;
}
  
  .claim-form-2 button {
    width: 60%;
    height: 45px;
    background: #00000080;
    border: none;
    outline: none;
    border-radius: 30px;
    box-shadow: 2px 2px 8px rgba(16, 16, 16, 0.696);
    color: #000000c9;
    font-size: 16px;
    cursor: pointer;
    font-weight: 700;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    transition: transform 0.2s ease;
    margin-top: 5px;
    z-index: 1;
  }
  
  .claim-form-2 button:hover {
    background: #202020;
    color: #bcbcbc;
  }
  
  .claim-bg2{
    margin-top: 5vh;
    margin-left: 30vh;
    background-color: #2b2b2b;
    width:40%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1005;
    height: 89vh;
    border: 6px ridge #000000;
    box-shadow: 0 0 18px rgb(0, 0, 0);

  }

  .claim-bg2 video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; 

  }

  .claim-bg2 p{
    color: #e9e9e9;
    text-align: left;
    text-shadow: 2px 2px 3px rgb(0, 0, 0);
    font-size: 22px;
    margin-bottom: 5px;
    margin-top: 54%;
    margin-left: 15px;
    font-weight: lighter;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }

  .claim-bg2-1{
    background-color: #30303093;
    width:39%;
    z-index: 1009;
    position: absolute;
    left: 0;
    top: 0;
    height: 20vh;
    border: 6px ridge #000000b5;
    padding-top: 22px;
    box-shadow: 5px 2px 15px rgba(0, 0, 0, 0.555);
    backdrop-filter: blur(20px);
  }

  .claim-bg2-1 h1{
    color: azure;
    font-size: 44px;
    text-shadow: 
  -4px -1px 0 #00000063,  
   1px -1px 0 #00000063,
  -1px  1px 0 #00000063,
   1px  1px 0 #00000063;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }

  .side-2{
    background: url("C:\Users\ungati_mohith\Downloads\react-website\react-website-v1-master\public\images\img-20.jpg") center center/cover no-repeat;
    width:49%;
    z-index: 1001;
    position: absolute;
    bottom: 0;
    right: 0;
    height: 40vh;
    border: 6px ridge #000000;
    padding: 22px;
    box-shadow: 0 0 18px rgba(0, 0, 0, 0.845);
  }