.admin-dashboard {
    all: unset;
    animation: fadeIn 2s ease-in;
  }

    @keyframes pop {
    0% {
      transform: scale(0);
      opacity: 1;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }}
  
  @keyframes fadeIn {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }

  .main-content h1{
    width: 100%;
    height: 100%;
    color: #000;
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: normal;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .function-card:nth-child(1) {
    animation-delay: 0.5s;
  }
  
  .function-card:nth-child(2) {
    animation-delay: 0.7s;
  }
  
  .function-card:nth-child(3) {
    animation-delay: 0.9s;
  }
  
  @keyframes popIn {
    0% {
      transform: scale(0.9);
      opacity: 0;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
  
  .function-card h3 {
    font-size: 24px;
    font-weight: bold;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  .function-card p {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  .function-card .btn {
    background-color: #f1f1f1;
    opacity: 90%;
    border: 1.5px solid #000000;
    border-radius: 30px;
    color: #380b0c;
    font-family: "Oswald", sans-serif;
    font-weight: normal;
    font-size: 2.6vh;
    padding: 8px 10px 8px 10px;
    box-shadow: 0px 0px 5px rgba(89, 89, 89, 0.358);
    cursor: pointer;
    transition: transform 0.2s ease;
    display: block;
    text-decoration: none;
    width: 50%;
    margin: 0;
    padding: 0;
  }
  
  .function-card .btn:hover {
    transform: translateY(-5px); 
    background-color: #6d1718;
    box-shadow: 2px 2px 10px rgba(89, 89, 89, 0.537);
    opacity: 100%;
    color: #f1f1f1;
  }
  